<template>
  <div class="main">
    <el-container>
      <el-header>
        <div class="mainTitle">
          <img src="../../assets/images/dataTitle.png" alt />
        </div>
      </el-header>
      <el-container>
        <el-aside width="55%">
          <div class="chartsBlock">
            <div class="blockTit">
              <img src="../../assets/images/dh.png" alt="" />
              <span>男女党员比例</span>
            </div>
            <div class="blockTit">
              <div class="markPointLeft">
                <p></p>
                <span>男</span>
              </div>
              <div class="markPointRight">
                <p></p>
                <span>女</span>
              </div>
            </div>
            <vue-seamless-scroll
              :data="listData"
              class="seamless-warp"
              :class-option="classOption"
            >
              <ul class="ul-scoll">
                <li v-for="(item, index) in listData" :key="index">
                  <div class="orgName">
                    {{ item.orgName }}
                  </div>
                  <div class="orgContent">
                    <div class="manScale" :style="{ width: item.man }">
                      {{ item.man }}
                    </div>
                    <div class="womanScale" :style="{ width: item.women }">
                      {{ item.women }}
                    </div>
                  </div>
                </li>
              </ul>
            </vue-seamless-scroll>
          </div>
          <div class="chartsBlock">
            <div class="blockTit">
              <img src="../../assets/images/dh.png" alt="" />
              <span>党员统计</span>
            </div>
            <div class="tableTit">
              <div>序号</div>
              <div>名称</div>
              <div>人数</div>
              <div>占比</div>
            </div>
            <vue-seamless-scroll
              :data="listData1"
              class="seamless-warp"
              :class-option="classOption1"
            >
              <ul class="ul-scoll-scale">
                <li
                  v-for="(item, index) in listData1"
                  :key="index"
                  :class="{ backgroundColor: index % 2 == 0 }"
                >
                  <span>{{ index + 1 }}</span>
                  <span>{{ item.orgName }}</span>
                  <span>{{ item.num }}</span>
                  <span>{{ item.scale }}</span>
                </li>
              </ul>
            </vue-seamless-scroll>
          </div>
        </el-aside>
        <el-main>
          <div class="campass">
            <img src="../../assets/images/campass.png" alt />
          </div>
          <div class="map">
            <img src="../../assets/images/mapData.png" alt />
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import vueSeamlessScroll from "vue-seamless-scroll";
export default {
  data() {
    return {
      listData: [
        {
          orgName: "朱家沟采油队",
          man: "76%",
          women: "24%",
        },
        {
          orgName: "朱家沟采油队",
