          women: "24%",
        },
        {
          orgName: "朱家沟采油队",
          man: "76%",
          women: "24%",
        },
        {
          orgName: "朱家沟采油队",
          man: "33%",
          women: "67%",
        },
        {
          orgName: "朱家沟采油队",
          man: "76%",
          women: "24%",
        },
        {
          orgName: "朱家沟采油队",
          man: "10%",
          women: "90%",
        },
        {
          orgName: "朱家沟采油队",
          man: "76%",
          women: "24%",
        },
        {
          orgName: "朱家沟采油队",
          man: "50%",
          women: "50%",
        },
        {
          orgName: "朱家沟采油队",
          man: "66%",
          women: "34%",
        },
        {
          orgName: "朱家沟采油队",
          man: "22%",
          women: "78%",
        },
        {
          orgName: "朱家沟采油队",
          man: "65%",
          women: "35%",
        },
      ],
      listData1: [
        {
          orgName: "朱家沟采油队",
          num: "10000",
          scale: "24%",
        },
        {
          orgName: "朱家沟采油队",
          num: "10000",
          scale: "24%",
        },
        {
          orgName: "朱家沟采油队",
          num: "10000",
