<style scoped>
.blockTit {
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tableTit {
  height: 30px;
  background-color: #c10001;
  display: flex;
  justify-content: space-between;
}
.tableTit div {
  flex: 1;
  color: #fff;
  line-height: 30px;
  text-align: center;
  font-size: 18px;
}
.blockTit span {
  color: #ebd71f;
  font-size: 22px;
}
.blockTit img {
  margin-right: 5px;
}
.markPointLeft p {
  border-radius: 3px;
  width: 30px;
  height: 20px;
  background-color: #f8f400;
}
.markPointLeft span {
  color: #fff;
}
.markPointRight p {
  border-radius: 3px;
  width: 30px;
  height: 20px;
  background-color: #391acb;
}
.markPointLeft,
.markPointRight {
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.markPointRight span {
  color: #fff;
}
.seamless-warp {
  height: 290px;
  overflow: hidden;
  border-radius: 5px;
}
.main {
  height: 100%;
  min-height: 900px;
  background: url("../../assets/images/bigData.png") no-repeat;
}
.el-container {
  height: 100%;
}
.el-main {
  height: 100%;
  min-width: 800px;
  position: relative;
  overflow: hidden;
}
.el-header{
  width: 100%;
  min-width: 50%
}
.el-header img {
  width: 100%;
}
.el-aside {
  height: 100%;
  min-width: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.campass {
  width: 200px;
  height: 200px;
  position: absolute;
  top: 10%;
  right: 10%;
}
.campass img {
  width: 100%;
}
.map {
  height: 100%;
  position: absolute;
  left: 5%;
}
.map img {
  height: 100%;
}
.chartsBlock {
  padding: 20px 50px;
  width: 80%;
  height: 390px;
  background: url("../../assets/images/bgBlock.png") no-repeat;
  background-size: 100% 100%;
}
.ul-scoll {
  width: 100%;
  background-color: #7e1816;
  padding: 0 20px 0 0;
}
.ul-scoll li {
  display: flex;
  height: 30px;
  font-weight: 700;
  align-items: center;
  justify-content: space-between;
}
.orgName {
  color: #fff;
  width: 180px;
  font-size: 16px;
  text-align: center;
}
.orgContent {
  flex: 1;
  display: flex;
  border-radius: 5px;
  background-color: #391acb;
}
.manScale {
  background-color: #f9f500;
  border-radius: 5px;
  height: 20px;
  line-height: 20px;
  color: #9d2605;
  text-align: center;
  font-size: 14px;
}
.womanScale {
  background-color: #391acb;
  border-radius: 5px;
  height: 20px;
  line-height: 20px;
  color: #fff;
  text-align: center;
  font-size: 14px;
}
.ul-scoll-scale {
  width: 100%;
}
.ul-scoll-scale li {
  width: 100%;
  display: flex;
  padding: 10px 0;
}
.ul-scoll-scale li span {
  flex: 1;
  text-align: center;
  color: #fff;
  font-size: 18px;
}
.backgroundColor {
  background-color: #5b1716;
}
</style>


